<template>
	<div class="login-out-wrapper">
		<div class="login-wrapper">
			<div class="login-logo">
				<img src="../assets/images/login_logo.png" />
			</div>
			<div class="login-content">
				<div class="login-title">登录</div>
				<div class="input-area">
					<div class="input-title">用户名：</div>
					<el-input placeholder="请输入" v-model="userName"></el-input>
				</div>
				<div class="input-area">
					<div class="input-title">密码：</div>
					<el-input
						placeholder="请输入密码"
						type="password"
						v-model="userPassword"
					></el-input>
				</div>
				<div class="fail-area">
					<div v-show="failFlag">
						<span>用户名或密码错误，请重新输入</span
						><img src="../assets/images/login-fail.png" class="fail-icon" />
					</div>
				</div>

				<div class="login-btn" @click="login">确定</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			userName: "",
			userPassword: "",
			failFlag: false,
		};
	},
	created() {},
	mounted() {
		window.addEventListener("keydown", this.keyDown);
	},
	computed: {},
	methods: {
		login() {
			if (this.userName == "") {
				this.$message({
					type: "warning",
					message: "请输入用户名！",
				});
				return;
			}
			if (this.userPassword == "") {
				this.$message({
					type: "warning",
					message: "请输入密码！",
				});
				return;
			}
			if (this.userName == "admin" && this.userPassword === "qwer1234") {
				sessionStorage.setItem("isLogin", true);
				this.$router.push("/productManage");
			} else {
				this.failFlag = true;
			}
		},
		keyDown(e) {
			if (e.keyCode == 13) {
				this.login();
			}
		},
	},
	destroyed() {
		window.removeEventListener("keydown", this.keyDown, false);
	},
};
</script>

<style lang="scss" scoped>
.login-out-wrapper {
	position: relative;
	width: 100vw;
	height: 100vh;
	background: url("../assets/images/login-bc.png") 100% 100% no-repeat;
	.login-wrapper {
		width: 1100px;
		height: 485px;
		border-radius: 4px;
		background: linear-gradient(
			to bottom,
			rgba(48, 60, 119, 1) 0%,
			rgba(48, 60, 119, 0.4) 100%
		);
		background-color: rgba(22, 90, 203, 0.4);
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		display: flex;
		border: none;
		.login-logo {
			flex: 1;
			width: 50%;
			box-sizing: border-box;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.login-content {
			height: 100%;
			background-color: #fff;
			padding: 50px 50px 0 50px;
			box-sizing: border-box;
			width: 50%;
			.login-title {
				font-size: 30px;
				font-weight: bold;
				margin-bottom: 30px;
				color: #313552;
			}
			.input-area {
				.el-input {
					height: 46px;
					/deep/.el-input__inner {
						background-color: #f9fbfd !important;
						height: 100%;
					}
				}
				.input-title {
					color: #8c8c8c;
					font-size: 16px;
					margin-bottom: 10px;
					margin-top: 27px;
				}
			}
			.fail-area {
				color: #e67070;
				position: relative;
				margin-top: 5px;
				height: 20px;
				.fail-icon {
					position: absolute;
					left: 100%;
					top: 50%;
					transform: translate(10px, -50%);
				}
			}
			.login-btn {
				width: calc(100% - 20px);
				height: 55px;
				line-height: 55px;
				background-color: #425acb;
				border-radius: 27.5px;
				color: #fff;
				font-size: 24px;
				text-align: center;
				margin-top: 50px;
				cursor: pointer;
			}
		}
	}
}
</style>
